<script setup>
import { defineProps, defineEmits } from 'vue';

const props = defineProps({
  currentIndex: Number,
  totalIndex: Number
});

const emit = defineEmits(['moveImage']);

const moveImage = (direction) => {
  emit('moveImage', direction);
};
